<template>
  <div id="avatar-wrapper"  :style="{ height: height + 'px', width : width + 'px'}">

      <div id="divVHSS"></div>

  </div>
</template>

<script>
export default {
  name: "avatar",
  props: {
    initialWidth: {
      type: Number,
      required: true
    }
    },

  data() {
    return{
        width: this.initialWidth,
    }
  },
  computed: {
    height: {
      get: function () {
        return this.width * 1.125;
      },
      set: function (newVal) {
        this.width = newVal * 8/9;
      }
    },
    spVoice: function() {
      switch (this.curLanguage) {
        case 'es':
          return 4;
        default: // en
          return 3;
      }
    },
    spLang: function() {
      switch (this.curLanguage) {
        case 'es':
          return 2;
        default: // en
          return 1;
      }
    },
    spEngine: function() {
      switch (this.curLanguage) {
        case 'es':
          return 4;
        default: // en
          return 3;
      }
    }
  },
  methods:{
    avatarResize(){
      console.log('New Width is ', this.width)
      console.log('New Height is', this.height)
      window.dynamicResize(this.width, this.height)
    },
    speak: function(speakText) {
      //this.curSpeech = speakText;
      console.log('Speeching Text', speakText)


      //const that = this;
      window.sayText('<prosody rate="+15%">' + speakText + '</prosody>', 3, 1, 3);


      //const that = this;
      //this.promise = new DeferredPromise();
      // if (this.curLanguage === 'en') {
      //   window.sayText('<prosody rate="+15%">' + speakText + '</prosody>', that.spVoice, that.spLang, that.spEngine);
      // }
      // else {
      //   window.sayText(speakText, that.spVoice, that.spLang, that.spEngine);
      // }

      return this.promise;
    },
    stopSpeak(){
      window.stopSpeech()
    }
  },
  watch: {
    width: function () {
      window.dynamicResize(this.width, this.height);
      console.log('canvas canvas canvas')
    },
  },
  mounted() {
    const that = this
    window.vh_sceneLoaded = function(){
      console.log('It came in here')
      that.avatarResize()
    }
  }
}
</script>

